import React, {Component} from 'react';
import {
    ComposableMap,
    ZoomableGroup,
    Geographies,
    Geography,
} from 'react-simple-maps';
import * as d3 from 'd3';
import ReactTooltip from 'react-tooltip';

import Mapdata from './world-50m.json';
import './Map.css';

const colorScale = d3.scaleLinear()
    //.domain([0, 900])
    .range(['#bdc9e1','#74a9cf','#2b8cbe','#045a8d']);

class Map extends Component {

    constructor() {
        super()

        this.state = {
            zoom: 1,
            mapData: null,
            showDefault: true,
            optimizationDisabled: false
        }

        this.handleZoomIn = this.handleZoomIn.bind(this);
        this.handleZoomOut = this.handleZoomOut.bind(this);
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        if (nextProps.mapData !== prevState.mapData) {
            return {mapData: nextProps.mapData};
        } else {
            return null;
        }
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.mapData !== this.props.mapData) {
            this.setState({mapData: this.props.mapData});
            this.addMapSalesDataHandler(this.state.mapData);
        }
    }

    addMapSalesDataHandler = (data) => {
        const md = Mapdata.objects.ne_50m_admin_0_countries.geometries;
        for (let i = 0; i < md.length; i++) {
            if (data.some(data => data.CountryCode === md[i].properties.ISO_A2)) {
                let dataCountryIndex = data.findIndex(data => data.CountryCode === md[i].properties.ISO_A2);
                Mapdata.objects.ne_50m_admin_0_countries.geometries[i].properties.NETUNITS = data[dataCountryIndex].NetUnits;
            } else {
                //Set the Net Units to Zero
                Mapdata.objects.ne_50m_admin_0_countries.geometries[i].properties.NETUNITS = 0;
            }
        }
        let maxSalesNum = Math.max.apply(Math, data.map((o) => { return o.NetUnits}));
        colorScale.domain([0, Math.round(maxSalesNum/4)]);
        this.loadNewData();
    }

    loadNewData = () => {
        this.setState({
            optimizationDisabled: true,
            showDefault: false, //CHANGE THIS (THIS IS WHAT CAUSES IT TO NOT DISPLAY)
          }, () => {
            this.setState({
              optimizationDisabled: false,
            })
          })
    }

    handleZoomIn() {
        this.setState({
            zoom: this.state.zoom * 2
        });
    }

    handleZoomOut() {
        this.setState({
            zoom: this.state.zoom / 2
        });
    }


    render() {

        return (
            <>
            <div className="MapRec">
                <ComposableMap className="Map"
                    projection="miller"
                    projectionConfig={{
                        scale: 140
                    }}>
                    <ZoomableGroup zoom={this.state.zoom}>
                        <Geographies geography={Mapdata} disableOptimization={ this.state.optimizationDisabled }>
                          {(geographies, projection) => geographies.map(geography=> (
                              <Geography
                                data-tip={geography.properties.NAME + " : " + geography.properties.NETUNITS + " Units"}
                                data-for="mapTooltip"
                                key={geography.id}
                                geography={geography}
                                projection={projection}
                                style={{
                                    default: {
                                        fill: this.state.showDefault ? '#bdc9e1' : colorScale(geography.properties.NETUNITS),
                                        stroke: 'white',
                                        strokeWidth: 1,
                                        outline: 'none'
                                    },
                                    hover: {
                                        fill: '#ff7f0e',
                                        stroke: 'white',
                                        strokeWidth: 1,
                                        outline: 'none'
                                    },
                                    pressed: {
                                        fill: '#ff7f0e',
                                        stroke: 'white',
                                        strokeWidth: 1,
                                        outline: 'none'
                                    }
                                }}
                              />
                          ))}
                        </Geographies>
                    </ZoomableGroup>
                </ComposableMap>
                <ReactTooltip id="mapTooltip"/>
            </div>
            <div className="ZoomBtnGroup">
                <button className="ZoomBtn" onClick={ this.handleZoomIn }>{ "+" }</button>
                <button className="ZoomBtn" onClick={ this.handleZoomOut }>{ "-" }</button>
            </div>
            </>
        );
    }
}

export default Map;