import React, {Component} from 'react';
import * as d3 from 'd3';
import ReactTooltip from 'react-tooltip';

import './LineGraph.css';

class LineGraph extends Component {
    constructor() {
        super()
        this.state = {
            firstRender: false
        }
    }

    componentDidMount() {
        this.drawLine(this.props.data);
    }

    componentDidUpdate() {
        this.drawLine(this.props.data);
    }

    drawLine = (data) => {
        const node = this.node;

        if (data !== null) {

            const margin = {top: 16, bottom: 32, left: 24, right: 8};
            let width = this.props.width - margin.left - margin.right, height = this.props.height - margin.bottom - margin.top;


            if (!this.state.firstRender) {
                this.setState({firstRender: true});
                // let width = document.getElementById(this.props.graphid).offsetWidth,
                // height = document.getElementById(this.props.graphid).offsetHeight;

                let monthList = [];
                let monthFormat = "";
                for (let i = 0; i < data.length; i++) {
                    monthFormat = data[i].Month.split('0')[1];
                    monthFormat = monthFormat.slice(0,6);
                    monthList.push(monthFormat);
                }

                let chargeBackData = [];
                for (let i = 0; i < data.length; i++ ) {
                    chargeBackData.push(data[i].ChargeBacks);
                }

                // const svg = d3.select(node) //'#' + this.props.graphid
                //             .append('svg')
                //             .attr('width', width + margin.left + margin.right)
                //             .attr('height', height + margin.top + margin.bottom)
                //             .attr('id', 'line')
                //             .attr('transform', 'translate(0, 0)');
                
                const xScale = d3.scaleLinear()
                            .domain([0, data.length])
                            .range([0 + margin.left, width - margin.right ]);
                
                const yScale = d3.scaleLinear()
                            .domain([0, d3.max(data, function(d) { return d.ChargeBacks; })])
                            .range([height - margin.top, margin.bottom]);

                let line = d3.line()
                            .x((d, i) => { 
                                return xScale(i); 
                            })
                            .y((d) => { return yScale(d) - margin.top; });

                d3.select(node).append('path')
                    .datum(chargeBackData)
                    .attr('class', 'line')
                    .attr('d', line);
                
                //X AXIS
                d3.select(node).append('g')
                    .attr('class', 'xaxis')
                    .attr('transform', 'translate(0,'+ (height - margin.bottom) + ')')
                    .call(d3.axisBottom(xScale)
                        .ticks(data.length)
                        .tickFormat((d,i) => {return monthList[i];}))
                    .selectAll("text")
                        .attr("y", 0)
                        .attr("x", 9)
                        .attr("dy", ".35em")
                        .attr("transform", "rotate(90)")
                        .style("text-anchor", "start");

                //Y AXIS
                d3.select(node).append('g')
                    .attr('class', 'yaxis')
                    .attr('transform', 'translate(' + (xScale(0)) + "," + (0 - margin.bottom*0.5) +")")
                    .call(d3.axisLeft(yScale)
                        .ticks(10));

                d3.select(node).selectAll('dot')
                    .data(data)
                    .enter()
                    .append('circle')
                    .attr('class', 'dot')
                    .attr('cx', function(d, i) { return xScale(i); })
                    .attr('cy', function(d, i) {
                        return yScale(d.ChargeBacks) - margin.top; 
                    })
                    .attr('r', 3)
                    .attr('data-tip', function(d) {
                        return d.ChargeBacks + " Chargebacks"
                    })
                    .attr('data-for', 'svgTooltip');
                    // .on('mouseover', function(d,i) {
                    // d3.select(this)
                    //     .transition()
                    //     .duration(100)
                    //     .style('fill', 'red')
                    //     .attr('r', 5);
                    // })
                    // .on('mouseout', function() {
                    //     d3.select(this)
                    //         .transition()
                    //         .duration(200)
                    //         .style('fill', 'yellow')
                    //         .attr('r', 2);
                    // });
            } else {
                //Update the Data
                let monthList = [];
                let monthFormat = "";
                for (let i = 0; i < data.length; i++) {
                    monthFormat = data[i].Month.split('0')[1];
                    monthFormat = monthFormat.slice(0,6);
                    monthList.push(monthFormat);
                }

                let chargeBackData = [];
                for (let i = 0; i < data.length; i++ ) {
                    chargeBackData.push(data[i].ChargeBacks);
                }

                const xScale = d3.scaleLinear()
                .domain([0, data.length])
                .range([0 + margin.left, width - margin.right ]);
    
                const yScale = d3.scaleLinear()
                            .domain([0, d3.max(data, function(d) { return d.ChargeBacks; })])
                            .range([height - margin.top, margin.bottom]);

                //X AXIS
                d3.select(node).select('.xaxis')
                    .attr('class', 'xaxis')
                    .attr('transform', 'translate(0,'+ (height - margin.bottom) + ')')
                    .call(d3.axisBottom(xScale)
                        .ticks(data.length)
                        .tickFormat((d,i) => {return monthList[i];}))
                    .selectAll("text")
                        .attr("y", 0)
                        .attr("x", 9)
                        .attr("dy", ".35em")
                        .attr("transform", "rotate(90)")
                        .style("text-anchor", "start");
            
                //Y AXIS
                d3.select(node).select('.yaxis')
                    .attr('class', 'yaxis')
                    .attr('transform', 'translate(' + (xScale(0)) + "," + (0 - margin.bottom*0.5) +")")
                    .call(d3.axisLeft(yScale)
                        .ticks(10));

                let line = d3.line()
                            .x((d, i) => { 
                                return xScale(i); 
                            })
                            .y((d) => { return yScale(d) - margin.top; });

                d3.select(node).select('.line')
                    .datum(chargeBackData)
                    //.attr('class', 'line')
                    .attr('d', line);

                d3.select(node).selectAll('circle')
                    .data(data)
                    .exit()
                    .remove();

                const dots = d3.select(node).selectAll('circle')
                            .data(data);

                d3.select(node).selectAll('circle')
                    .data(data)
                    .enter()
                    .append('circle')
                    .attr('cx', width)
                    .attr('cy', function(d, i) {
                        return yScale(d.ChargeBacks) - margin.top; 
                    })
                    .attr('r', 3)
                    .attr('data-tip', function(d) {
                        return d.ChargeBacks + " Chargebacks"
                    })
                    .attr('data-for', 'svgTooltip')
                    .merge(dots)
                    .transition()
                    .duration(500)
                    .attr('class', 'dot')
                    .attr('cx', function(d, i) { return xScale(i); })
                    .attr('cy', function(d, i) {
                        return yScale(d.ChargeBacks) - margin.top; 
                    })
                    .attr('r', 2)
                    .attr('data-tip', function(d) {
                        return d.ChargeBacks + " Chargebacks"
                    })
                    .attr('data-for', 'svgTooltip');

            }
        }
    }

    render() {
        return(
            <div className="Graph">
                <svg ref={node => this.node = node}
                className={this.props.graphID}
                width={this.props.width} height={this.props.height}>
                </svg>
                <ReactTooltip id="svgTooltip" />
            </div>
        );
    }
}

export default LineGraph;