import React, { Component } from 'react';

import Header from '../../components/Header/Header';
import Dashboard from '../Dashboard/Dashboard';
//import SideDrawer from '../../components/SideDrawer/SideDrawer';
import Modal from '../../components/Modal/Modal';
import FileDrop from '../../components/Modal/FileDrop/FileDrop';

class Layout extends Component {

    constructor(props) {
        super(props);
        this.state = {
            uploading: false,
            rawData: null
        };

        this.openModalHander = this.openModalHander.bind(this);
        this.closeModalHandler = this.closeModalHandler.bind(this);
        //this.getRawDataHandler = this.getRawDataHandler.bind(this);
    }

    openModalHander() {
        this.setState({uploading: true})
    }

    closeModalHandler() {
        this.setState({uploading: false})
    }

    getRawDataHandler = (childRawData) => {
        this.setState({rawData: childRawData}, () => {
            //I DUNNO
        });
    }

    render () {

        return (
            <div className="Layout">
                <Modal show={this.state.uploading}
                modalClosed={this.closeModalHandler}>
                    <FileDrop modalClosed={this.closeModalHandler} sendData={this.getRawDataHandler}/>
                </Modal>
                <Header openmodal={this.openModalHander} />
                <Dashboard newData={this.state.rawData}/>
            </div>
        );
    }
}

export default Layout;