import React from 'react';

import './Header.css';

const header = (props) => {

    return(
        <div className="Header">
            <h1 className="Header-item">INDIEDASH</h1>
            <p className="Header-item">analytics for indies</p>
            <button
                className="Header-upload"
                onClick={props.openmodal}>upload</button>
        </div>
    );
}

export default header;