import React, {Component} from 'react';
import Dropzone from 'react-dropzone';
import Papa from 'papaparse';

import './FileDrop.css';

let showDropDialog = null;
const acceptedPlatforms = ['Steam', 'Itchio'];

class FileDrop extends Component {
    constructor() {
        super()
        this.state = {
            accepted: [],
            rejected: [],
            data: {},
            dropFileType : acceptedPlatforms[0]
        }

      this.onDrop = this.onDrop.bind(this);
      this.clickModalClosed = this.clickModalClosed.bind(this);
    }

    onDrop = (acceptedFiles, rejectedFiles) => {

        this.setState({accepted: acceptedFiles, rejected: rejectedFiles});

        const reader = new FileReader();
        reader.onload = () => {
            const fileAsText = reader.result;
            let newFileAsText = null;
            let newData = this.state.data;
            let platformID = 0;
            let gameName = "";

            switch(this.state.dropFileType) {
              case acceptedPlatforms[0]:
                  let lines = fileAsText.split('\n');
                  lines.splice(0,3);
                  newFileAsText = lines.join('\n'); 
                  platformID = 0;
                  break;
              case acceptedPlatforms[1]:
                  newFileAsText = fileAsText;
                  platformID = 1;
                  break;
              default: 
                  newFileAsText = fileAsText;
                  break;
            }

            //Parse through the data and return object
            const data = Papa.parse(newFileAsText, {
                header: true,
                skipEmptyLines: true,
                dynamicTyping: true
            });

            if (platformID === 0) {
                gameName = data.data[0]['Game'];
            }
            else if (platformID === 1) {
                gameName = data.data[0]['object_name'];
            }
            let newObj = {};
            newObj[gameName] = data;
            newData[this.state.dropFileType] = newObj;
            this.setState({data: newData});
        };
        reader.onabort = () => console.log('file reading was aborted');
        reader.onerror = () => console.log('file reading has failed');
        
        for (let file in acceptedFiles) {
          reader.readAsText(acceptedFiles[file]);
        }
    }

    clickModalClosed() {
        //Close the modal and send the raw data to parent
        this.props.sendData(this.state.data);
        this.props.modalClosed();
    }

    fileTypeHandler = (e) => {
        this.setState({dropFileType: e.target.value});
    }

    render() {

        if (this.state.accepted.length > 0 || this.state.rejected.length > 0) {
          showDropDialog = (
            <div>
              <h4>Accepted Files</h4>
              <ul>
                  {this.state.accepted.map(f => <li key={f.name}>{f.name} - {f.size} bytes</li>)}
              </ul>
              <h4>Rejected Files</h4>
              <ul>
                  {this.state.rejected.map(f => <li key={f.name}>{f.name} - {f.size} bytes</li>)}
              </ul>
              <button 
                className="FileDropBtn"
                onClick={this.clickModalClosed}>All Done!</button>
            </div>
          );
        }

        const baseStyle = {
            width: '100%',
            height: '100%',
            borderWidth: 2,
            borderColor: 'blue',
            borderStyle: 'solid',
            borderRadius: 2,
            textAlign: 'center'
        };
        const activeStyle = {
            borderColor: 'lime',
            backgroundColor: '#eee'
        };

        return(
          <section>
            <select onChange={this.fileTypeHandler} id="fileType" name="File Type">
              <option value={acceptedPlatforms[0]}>{acceptedPlatforms[0]}</option>
              <option value={acceptedPlatforms[1]}>{acceptedPlatforms[1]}</option>
            </select>
            <Dropzone accept=".csv" onDrop={this.onDrop}>
              {({getRootProps, getInputProps, isDragActive}) => {
                  let styles = {...baseStyle}
                  styles = isDragActive ? {...styles, ...activeStyle} : styles
                  return (
                    <div
                        {...getRootProps()}
                        style={styles}
                    >
                      <input {...getInputProps()} />
                      {
                          isDragActive ?
                            <p>Drop files here...</p> :
                            <p>Try dropping some files here, or click to select files to upload.</p>
                      }
                    </div>
                  );
              }}
            </Dropzone>
            <aside>
              {showDropDialog}
            </aside>
        </section>
        );
    }

}

export default FileDrop;