import React, { Component } from 'react';
import moment from 'moment';

import BarGraph from './BarGraph/BarGraph';
import Map from './Map/Map';
import Table from './Table/Table';
import DashSection from './DashSection/DashSection';
import LineGraph from './LineGraph/LineGraph';
import PieChart from './PieChart/PieChart';
//import FilterMenu from './FilterMenu/FilterMenu';
import FilterTop from './FilterTop/FilterTop';
import MapData from './Map/world-50m.json';

import './Dashboard.css';

let storeFilters = <option value="All">All Stores</option>;
let gameFilters = <option value="All">All Games</option>;
let timeFiltersStart = <option value="Start">Start</option>;
let timeFiltersEnd = <option value="End">End</option>;

class Dashboard extends Component {
    constructor() {
        super()
        this.state = {
            rawData : {},
            tableMapData: null,
            regionData: null,
            timeSeriesData: null,
            chargebackData: null,
            platformData: null,
            mapList: null,
            filters: {
                store: 'All Stores',
                game: 'All Games',
                dateStart: 'Start',
                dateEnd: 'End',
                country: 'All',
                region: 'All',
                platform: 'All'
            }
        }
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        if (nextProps.newData !== prevState.rawData) {
            return {rawData: nextProps.newData};
        } else {
            return null;
        }
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.newData !== this.props.newData) {
            this.setState({rawData: this.props.newData});
            this.setTopFiltersHandler(this.state.rawData);
            this.calculateComponentData();
        }
    }

    setNewFiltersHandler = (event, category) => {
        let newFilters = {
            store: this.state.filters.store,
            game: this.state.filters.game,
            dateStart: this.state.filters.dateStart,
            dateEnd: this.state.filters.dateEnd,
            country: this.state.filters.country,
            region: this.state.filters.region,
            platform: this.state.filters.platform
        }
        newFilters[category] = event.target.value;
        this.setState({filters: newFilters}, () => {
            this.calculateComponentData();
        });
    }

    retrieveFilteredData = (data, filters) => {
        //Retrive all data depending on the filters and return it formatted
        let filteredData = [];
        let storeList = [];
        let gameList = [];
        let newDataRow = {};
        let mapList = [];

        //STORE FILTERS
        if (filters.store === 'All Stores') {
            //Iterate through all stores
            let storeNames = Object.keys(data)
            for (let s in storeNames) {
                storeList.push(storeNames[s]);
            }
        } else {
            //Iterate through the selected store
            storeList.push(filters.store);
        }

        //GAME FILTERS
        if (filters.game === 'All Games') {
            //Iterate through all games
            let gameNames = [];
            for (let s in storeList) {
                gameNames = Object.keys(data[storeList[s]]);
                for (let g in gameNames) {
                    gameList.push(gameNames[g]);
                }
            }
        } else {
            //Iterate through the selected game
            gameList.push(filters.game);
        }
        gameList = gameList.filter((value, index, self) => {
            return self.indexOf(value) === index;
        });

        let dataPoint, dateName;
        for (let store in storeList) {
            for (let game in gameList) {
                //Crunch data and format it
                for (let i = 0; i < data[storeList[store]][gameList[game]].data.length; i++) {
                    dataPoint = data[storeList[store]][gameList[game]].data;
                    if (storeList[store] === 'Itchio') {
                        data[storeList[store]][gameList[game]].data[i]['created_at'] = data[storeList[store]][gameList[game]].data[i]['created_at'].split(' ')[0];
                        dateName = 'created_at';
                    } else if (storeList[store] === 'Steam') {
                        dateName = 'Date';
                    }
                    if ((!moment(dataPoint[i][dateName]).isBefore(this.state.filters.dateStart, 'month')) && (!moment(dataPoint[i][dateName]).isAfter(this.state.filters.dateEnd, 'month'))) {
                        switch(storeList[store]) {
                            case 'Steam':
                                newDataRow = {
                                    Date: data[storeList[store]][gameList[game]].data[i]['Date'],
                                    Game: data[storeList[store]][gameList[game]].data[i]['Game'],
                                    Store: 'Steam',
                                    Platform: data[storeList[store]][gameList[game]].data[i]['Platform'],
                                    CountryCode: data[storeList[store]][gameList[game]].data[i]['Country Code'],
                                    Country: data[storeList[store]][gameList[game]].data[i]['Country'],
                                    Region: data[storeList[store]][gameList[game]].data[i]['Region'],
                                    Chargebacks: data[storeList[store]][gameList[game]].data[i]['Chargeback/Returns'],
                                    ChargebacksUSD: data[storeList[store]][gameList[game]].data[i]['Chargeback/Returns (USD)'],
                                    NetUnits: data[storeList[store]][gameList[game]].data[i]['Net Units Sold'],
                                    NetSales: data[storeList[store]][gameList[game]].data[i]['Net Steam Sales (USD)']
                                }
                                break;
                            case 'Itchio':
                                //Get Country Name from Country Code
                                let cName = '';
                                const md = MapData.objects.ne_50m_admin_0_countries.geometries;
                                for (let j = 0; j < md.length; j++) {
                                    for (let k=0; k < data[storeList[store]][gameList[game]].data.length; k++) {
                                        if (data[storeList[store]][gameList[game]].data[k]['country_code'] === md[j].properties.ISO_A2) {
                                            cName = md[j].properties.NAME;
                                            data[storeList[store]][gameList[game]].data[k]['country'] = cName;
                                        }
                                    }
                                }
                                newDataRow = {
                                    Date: data[storeList[store]][gameList[game]].data[i]['created_at'],
                                    Game: data[storeList[store]][gameList[game]].data[i]['object_name'],
                                    Store: 'Itchio',
                                    Platform: 'N/A',
                                    CountryCode: data[storeList[store]][gameList[game]].data[i]['country_code'],
                                    Country: data[storeList[store]][gameList[game]].data[i]['country'],
                                    Region: 'N/A',
                                    Chargebacks: 'N/A',
                                    ChargebacksUSD: 'N/A',
                                    NetUnits: 1,
                                    NetSales: data[storeList[store]][gameList[game]].data[i]['amount_delivered']
                                }
                                break;
                            default: break;
                        }
                        filteredData.push(newDataRow);
                    }
                }
            }
        }
        return filteredData;
    }

    calculateComponentData = () => {
        //Calculate all the data for the various components and store it into state
        let rawFilteredData = this.retrieveFilteredData(this.state.rawData, this.state.filters);

        //NOW CALCULATE ALL DATA FOR COMPONENTS
        this.calculateTableMapData(rawFilteredData);
        this.calculateRegionData(rawFilteredData);
        this.calculatePlatformData(rawFilteredData);
        this.calculateTimeSeriesData(rawFilteredData);
        this.calculateChargebackData(rawFilteredData);
    }

    calculateTableMapData = (data) => {
        let tableMapData = [];
        let countryCodeList = [];
        let newObject = {};

        for (let i = 0; i < data.length; i++) {
            countryCodeList.push(data[i].CountryCode);
        }
        countryCodeList = countryCodeList.filter((value, index, self) => {
            return self.indexOf(value) === index;
        });
        //Init objects
        for (let i = 0; i < countryCodeList.length; i++) {
            newObject = {
                CountryCode: countryCodeList[i],
                Country: "",
                NetUnits: 0,
                NetSales: 0
            }
            tableMapData.push(newObject);
        }
        //Fill the objects
        for (let i =0; i < tableMapData.length; i++) {
            for (let j = 0; j < data.length; j++) {
                if (tableMapData[i].CountryCode === data[j].CountryCode) {
                    if (tableMapData[i].Country === "") {
                        tableMapData[i].Country = data[j].Country;
                    }
                    tableMapData[i].NetUnits += data[j].NetUnits;
                    tableMapData[i].NetSales += data[j].NetSales;
                }
            }
        }
        //Make sure all the net sales are at two decimal places
        for (let i = 0; i < tableMapData.length; i++) {
            tableMapData[i].NetSales = tableMapData[i].NetSales.toFixed(2);
        }
        this.setState({tableMapData: tableMapData});
    }

    calculateRegionData = (data) => {
        let regionData = [];
        let regionList = [];
        let newObject = {};

        for (let i = 0; i < data.length; i++) {
            regionList.push(data[i].Region)
        }
        regionList = regionList.filter((value, index, self) => {
            return self.indexOf(value) === index;
        });
        //console.log(regionList);
        for (let i=0; i < regionList.length; i++) {
            newObject = {
                Region: regionList[i],
                NetUnits: 0,
                //NetSales: 0
            }
            regionData.push(newObject);
        }
        for (let i = 0; i < regionData.length; i++) {
            for (let j = 0; j < data.length; j++) {
                if (regionData[i].Region === data[j].Region) {
                    regionData[i].NetUnits += data[j].NetUnits;
                    //regionData[i].NetSales += data[j].NetSales;
                }
            }
        }
        //Fixed the net sales tofixed
        // for (let i=0; i < regionData.length; i++) {
        //     regionData[i].NetSales = regionData[i].NetSales.toFixed(2);
        // }
        this.setState({regionData: regionData},() => {
            //console.log(this.state.regionData);
        });
    }

    calculateChargebackData = (data) => {
        let chargeData = [];
        let dateList = [];
        let newObject = {};
        let monthDate = null;

        for (let i = 0; i < data.length; i++) {
            if (data[i].Store === 'Itchio') {
                data[i].Date = data[i].Date.split(' ')[0];
            }
            monthDate = moment(data[i].Date).format('YYYY MMMM');
            dateList.push(monthDate);
        }
        dateList = dateList.filter((value, index, self) => {
            return self.indexOf(value) === index;
        });

        for (let i=0; i < dateList.length; i++) {
            newObject = {
                Month: dateList[i],
                ChargeBacks: 0,
                ChargeBacksUSD: 0
            }
            chargeData.push(newObject);
        }
        for (let i = 0; i < chargeData.length; i++) {
            for (let j = 0; j < data.length; j++) {
                monthDate = moment(data[j].Date).format('YYYY MMMM');
                if (monthDate === chargeData[i].Month) {
                    if (data[j].Store !== 'Itchio') {
                        chargeData[i].ChargeBacks += data[j].Chargebacks;
                        chargeData[i].ChargeBacksUSD += data[j].ChargebacksUSD;
                    }
                }
            }
        }
        this.setState({chargebackData: chargeData}, () => {
            //console.log(this.state.chargebackData);
        })

    }

    calculateTimeSeriesData = (data) => {
        let timeData = [];
        let dateList = [];
        let newObject = {};
        let monthDate = null;

        for (let i = 0; i < data.length; i++) {
            // if (data[i].Store === 'Itchio') {
            //     data[i].Date = data[i].Date.split(' ')[0];
            //     //console.log(data[i].Date);
            // }
            monthDate = moment(data[i].Date).format('YYYY MMMM');
            dateList.push(monthDate);
        }
        dateList = dateList.filter((value, index, self) => {
            return self.indexOf(value) === index;
        });

        for (let i=0; i < dateList.length; i++) {
            newObject = {
                Month: dateList[i],
                NetUnits: 0,
                NetSales: 0
            }
            timeData.push(newObject);
        }

        for (let i = 0; i < timeData.length; i++) {
            for (let j = 0; j < data.length; j++) {
                monthDate = moment(data[j].Date).format('YYYY MMMM');
                if (monthDate === timeData[i].Month) {
                    timeData[i].NetUnits += data[j].NetUnits;
                    timeData[i].NetSales += data[j].NetSales;
                }               
            }
        }

        for (let i=0; i < timeData.length; i++) {
            timeData[i].NetSales = timeData[i].NetSales.toFixed(2);
        }
        this.setState({timeSeriesData: timeData}, () => {
            //console.log(this.state.timeSeriesData);
        });

    }

    calculatePlatformData = (data) => {
        let platformData = [];
        let platformList = [];
        let newObject = {};

        for (let i = 0; i < data.length; i++) {
            platformList.push(data[i].Platform);
        }
        platformList = platformList.filter((value, index, self) => {
            return self.indexOf(value) === index;
        });
        for (let i = 0; i < platformList.length; i++) {
            newObject = {
                Platform: platformList[i],
                NetUnits: 0,
                //NetSales: 0
            }
            platformData.push(newObject);
        }
        for (let i =0; i <platformData.length; i++) {
            for (let j = 0; j < data.length; j++) {
                if (platformData[i].Platform === data[j].Platform) {
                    platformData[i].NetUnits += data[j].NetUnits;
                    //platformData[i].NetSales += data[j].NetSales;
                }
            }
        }
        //Fixed the net sales tofixed
        // for (let i=0; i < platformData.length; i++) {
        //     platformData[i].NetSales = platformData[i].NetSales.toFixed(2);
        // }
        this.setState({platformData: platformData},() => {
            //console.log(this.state.platformData);
        });
    }

    setTopFiltersHandler = (data) => {
        let storeFrontList = ['All Stores'],
            gameList = ['All Games'],
            timeList = [],
            //timeKeyList = [],
            storeKeyList = [],
            gameKeyList = [],
            monthDate = null;

        //SET STOREFRONT FILTERS
        storeKeyList = Object.keys(data);
        for (let key in storeKeyList) {
            storeFrontList.push(storeKeyList[key]);
        }
        storeFilters = this.createSelectItems(storeFrontList);

        //SET GAME FILTERS
        for (let store in storeKeyList) {
            gameKeyList = Object.keys(data[storeKeyList[store]]);
            for (let key in gameKeyList) {
                gameList.push(gameKeyList[key]);
            }
        }
        gameList = gameList.filter((value, index, self) => {
            return self.indexOf(value) === index;
        });
        gameFilters = this.createSelectItems(gameList)

        //SET TIME FILTERS
        for (let store in storeKeyList) {
            for (let game in gameKeyList) {
                for (let i = 0; i < data[storeKeyList[store]][gameKeyList[game]].data.length; i++) {
                    if (storeKeyList[store] === 'Steam') {
                        monthDate = moment(data[storeKeyList[store]][gameKeyList[game]].data[i].Date).format('YYYY MMMM');                       
                    } else if (storeKeyList[store] === 'Itchio') {
                        data[storeKeyList[store]][gameKeyList[game]].data[i]['created_at'] = data[storeKeyList[store]][gameKeyList[game]].data[i]['created_at'].split(' ')[0];
                        monthDate = moment(data[storeKeyList[store]][gameKeyList[game]].data[i]['created_at']).format('YYYY MMMM');
                    }
                    timeList.push(monthDate);
                }
            }
        }
        timeList = timeList.filter((value, index, self) => {
            return self.indexOf(value) === index;
        });

        //Set the Initial Date States
        let newFilters = {
            store: this.state.filters.store,
            game: this.state.filters.game,
            dateStart: this.state.filters.dateStart,
            dateEnd: this.state.filters.dateEnd,
            country: this.state.filters.country,
            region: this.state.filters.region,
            platform: this.state.filters.platform
        }
        newFilters.dateStart = timeList[0];
        newFilters.dateEnd = timeList[timeList.length - 1];
        this.setState({filters: newFilters});

        timeFiltersStart = this.createSelectItems(timeList);
        timeFiltersEnd = this.createSelectItems(timeList);
    }

    createSelectItems = (array) => {
        let items = [];
        for (let i = 0; i <= array.length; i++) {
            items.push(<option key={i} value={array[i]}>{array[i]}</option>)
        }
        return items;
    }


    render () {
        return (
            <div className="Dashboard">
                <FilterTop>
                    Show Me Sales Data For 
                        <select className="SelectBox" onChange={(a) => {
                            this.setNewFiltersHandler(a, 'game');
                        }}
                        value={this.state.filters.game}>
                            {gameFilters}
                        </select> 
                    On 
                        <select className="SelectBox" onChange={(a) => {
                            this.setNewFiltersHandler(a, 'store');
                        }}
                        value={this.state.filters.store}>
                            {storeFilters}
                        </select>
                    From
                        <select className="SelectBox" onChange={(a) => {
                            this.setNewFiltersHandler(a, 'dateStart');
                        }}
                        value={this.state.filters.dateStart}>
                            {timeFiltersStart}
                        </select>
                    To
                        <select className="SelectBox" onChange={(a) => {
                            this.setNewFiltersHandler(a, 'dateEnd');
                        }}
                        value={this.state.filters.dateEnd}>
                            {timeFiltersEnd}
                        </select>
                </FilterTop>
                <DashSection width={"40%"}>
                    <h4>Sales By Country</h4>
                    <Map mapData={this.state.tableMapData}/>
                    <p style={{fontSize: 12}}>Net Units Sold By Month</p>
                    <BarGraph graphid="BarGraph" height={270} width={520} data={this.state.timeSeriesData}/>
                </DashSection>

                <DashSection width={"30%"}>
                    <h4>Sales Table</h4>
                    <Table tableData={this.state.tableMapData}/>
                    <p style={{fontSize: 12, marginTop: '32px'}}>Chargebacks/Returns by Month</p>
                    <LineGraph graphid={"LineGraph"} width={400} height={280} data={this.state.chargebackData}/>
                </DashSection>

                <DashSection width={"18%"}>
                    <div className="pieSection">
                        <p style={{fontSize: 12}}>Net Units Sold By Region</p>
                        <PieChart graphID="pieRegion" pieData={this.state.regionData} width={200} height={200}/>
                    </div>
                    <div className="pieSection">
                        <p style={{fontSize: 12}}>Count of Platform By Platform</p>
                        <PieChart graphID="piePlatform" pieData={this.state.platformData} width={200} height={200}/>
                    </div>
                </DashSection>
            </div>
        );
    }
}

export default Dashboard;