import React, {Component} from 'react';
import * as d3 from 'd3';
import ReactTooltip from 'react-tooltip';

//import Tooltip from '../../../components/Tooltip/Tooltip';

import './PieChart.css';

class PieChart extends Component {
    constructor() {
        super()
        this.state = {
            pieData : null
        }
        this.drawPie = this.drawPie.bind(this);
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        if (nextProps.pieData !== prevState.pieData) {
            return {pieData: nextProps.pieData};
        } else {
            return null;
        }
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.pieData !== this.props.pieData) {
            this.setState({pieData: this.props.pieData}, () => {
                //console.log(this.state.pieData);
            });
            this.drawPie(this.state.pieData);
        }
        ReactTooltip.rebuild();
    }

    // componentDidMount() {
    //     this.drawPie();
    // }

    drawPie(data) {
        const node = this.node;
        d3.select('donut').remove();

        let width = this.props.width, height = this.props.height;
        let radius = Math.min(width, height)/2,
            color = d3.scaleOrdinal(d3.schemeCategory10);

        const innerRadius = radius - 40;
        const outerRadius = radius - 10;

        const arc = d3.arc()
                    .innerRadius(innerRadius)
                    .outerRadius(outerRadius);

        const pie = d3.pie()
                        .value((d) => { return d.NetUnits });

        // let width = document.getElementById(this.props.graphid).offsetWidth,
        //     height = document.getElementById(this.props.graphid).offsetHeight

        const svg = d3.select(node) //'#' + this.props.graphid
                .append('svg')
                .attr('width', width)
                .attr('height', height)
                .attr('id', 'donut')
                .attr('transform', 'translate(0, 0)');
        
        let arcs = svg.selectAll('g.arc')
                .data(pie(data))
                .enter()
                .append('g')
                .attr('class', 'arc')
                .attr('transform', 'translate(' + outerRadius + ', ' + outerRadius + ')');

        svg.selectAll('g.arc')
            .data(pie(data))
            .exit()
            .remove();

        arcs.append('path')
            .attr('fill', (d, i) => {
                return color(i);
            })
            .attr('d', arc)
            .attr('data-tip', (d) => {
                switch(this.props.graphID) {
                    case "pieRegion":
                        return d.data.Region + ": " + d.data.NetUnits + " Units";
                    case "piePlatform":
                        return d.data.Platform + ": " + d.data.NetUnits + " Units";
                    default:
                        return "Undefined";
                }
            })
            .attr('data-for', 'svgTooltip')
            .style('stroke', 'white');
            // .on("mouseover", function(d) {
            //     toolTipDisplay = "Region: " + d.data.Region + '\nNet Units: ' + d.data.NetUnits;
            //     //console.log(toolTipDisplay);
            // })
            // .on("mouseout", (d) => {
            //     toolTipDisplay = null;
            // });
    }

    toolTipHandler = (props) => {
        return (
            <div className="toolTip">
                <p>{props}</p>
            </div>
        );
    }

    render() {

        let varName = "My Region"

        return(
            <div className="PieBox">
                <svg ref={node => this.node = node}
                width={this.props.width} height={this.props.height}>
                </svg>
                <ReactTooltip id="svgTooltip"/>
            </div>
        );
    }
}

export default PieChart;