import React, { Component } from 'react';
import ReactTable from 'react-table';

import './Table.css';
import 'react-table/react-table.css'

class Table extends Component {
    constructor() {
        super()
        this.state = {
            tableData: null
        }
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        if (nextProps.tableData !== prevState.tableData) {
            return {tableData: nextProps.tableData};
        } else {
            return null;
        }
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.tableData !== this.props.tableData) {
            this.setState({tableData: this.props.tableData});
            this.calculatePercentage(this.state.tableData);
        }
    }

    calculatePercentage = (data) => {
        let salesTotal = 0;
        let percent = 0;
        for (let i = 0; i < data.length; i++) {
            salesTotal += data[i].NetUnits;
        }
        console.log(salesTotal);
        for (let i = 0; i < data.length; i++) {
            percent = Math.round((data[i].NetUnits/salesTotal)*100);
            data[i].Percent = percent;
        }
        console.log(data);
        this.setState({tableData: data});
    }

    render () {
        let data = [{}],
        rowNum = 10;
        if (this.state.tableData !== null) {
            data = this.state.tableData;
            rowNum = this.state.tableData.length;
        }

        const columns = [{
            Header: "Country",
            accessor: "Country"
        }, {
            Header: "% of Total",
            accessor: "Percent"
        }, {
            Header: "Net Units",
            accessor: 'NetUnits'
        }, {
            Header: "Net Sales (USD)",
            accessor: 'NetSales'
        }]

        return (
            <ReactTable
                className="-striped -highlight"
                style={{
                    height: '40%',
                    width: '100%',
                    background: 'white',
                    marginTop: '16px',
                    fontSize: '12px'
                }} 
                data={data}
                columns={columns}
                showPagination={false}
                loading={false}
                defaultSorted={[
                    {
                        id: 'NetUnits',
                        desc: true
                    }
                ]}
                defaultPageSize={100} //MAGIC NUMBER CHANGE THIS FOOL!
                minRows={100}
            />
        );
    }
}

export default Table;