import React from 'react';

import './DashSection.css';

/*
    Easily segment the dashboard vertically with these dash sections
    props {
        width,
        children
    }
*/

const dashSection = (props) => {

    return(
        <div 
            className="DashSection"
            style={{width: props.width}}>
            {props.children}
        </div>
    );
}

export default dashSection;