import React, { Component } from 'react';
import * as d3 from 'd3';
import ReactTooltip from 'react-tooltip';

import './BarGraph.css';

class BarGraph extends Component {
    constructor() {
        super()
        this.state = {
            firstRender : false
        }
    }

    // componentDidMount() {
    //     this.drawChart(this.props.data);
    // }

    componentDidUpdate() {
        this.drawChart(this.props.data);
    }

    drawChart = (data) => {
        const node = this.node;

        const margin = {top: 32, right: 8, bottom: 40, left: 32}
        let width = this.props.width - margin.left - margin.right, height = this.props.height - margin.bottom - margin.top;

        // const svg = d3.select(node) //'#' + this.props.graphid
        //     .append('svg')
        //     .attr('width', width + margin.left + margin.right)
        //     .attr('height', height + margin.top + margin.bottom)
        //     .attr('id', this.props.graphid);

        // const g = svg.append('g')
        //     .attr('transform', "translate(" + margin.left + "," + margin.top + ")");

        if (data !== null) {
            if (!this.state.firstRender) {
                this.setState({firstRender: true});
                // let width = document.getElementById(this.props.graphid).offsetWidth,
                //     height = document.getElementById(this.props.graphid).offsetHeight

                //Abbreviate Month Names and Years From Data
                let monthList = [];
                let monthFormat = "";
                for (let i = 0; i < data.length; i++) {
                    monthFormat = data[i].Month.split('0')[1];
                    monthFormat = monthFormat.slice(0,6);
                    monthList.push(monthFormat);
                }

                const xScale = d3.scaleBand()
                                .domain(d3.range(data.length))
                                .range([0 + margin.left, width - margin.right])
                                .round(true)
                                .paddingInner(0.1);

                const yScale = d3.scaleLinear()
                                .domain([0, d3.max(data, function(d) { return d.NetUnits; })])
                                .range([height - margin.bottom, 0]);

                // Y-AXIS
                d3.select(node).append('g')
                    .attr('transform', 'translate(' + (xScale(0) - 4) + "," + (margin.top + 4) + ")") // - margin.left - 4
                    .attr('class', 'yaxis')
                    .call(d3.axisLeft(yScale)
                        .ticks(10, 's'));

                // X-AXIS
                d3.select(node).append('g')
                    .attr('transform', 'translate(' + (-2) +' , ' + (height - 4) + ")") //x: (-xScale.bandwidth()) y: - margin.bottom
                    .attr('class', 'xaxis')
                    .call(d3.axisBottom(xScale)
                        .ticks(data.length)
                        .tickFormat((d,i) => {return monthList[i];}))
                    .selectAll("text")
                        .attr("y", 0)
                        .attr("x", 9)
                        .attr("dy", ".35em")
                        .attr("transform", "rotate(90)")
                        .style("text-anchor", "start");

                d3.select(node).selectAll('rect')
                    .data(data)
                    .exit()
                    .remove();
                
                d3.select(node).selectAll('rect')
                    .data(data)
                    .enter()
                    .append('rect')
                    .attr('class', 'bar')
                    .attr('x', function(d, i) { 
                        return xScale(i); 
                    })
                    .attr('y', function(d) { return yScale(d.NetUnits) + margin.bottom - 4; }) //height - 8 - ...
                    .attr("width", xScale.bandwidth())
                    .attr("height", (d) => {
                        return yScale(0) - yScale(d.NetUnits);
                    })
                    .attr('data-tip', (d) => {
                        return d.NetUnits + " Net Units";
                    })
                    .attr('data-for', 'svgTooltip')
                    .style('fill', '#d62728');

            } else {

                let monthList = [];
                let monthFormat = "";
                for (let i = 0; i < data.length; i++) {
                    monthFormat = data[i].Month.split('0')[1];
                    monthFormat = monthFormat.slice(0,6);
                    monthList.push(monthFormat);
                }

                const xScale = d3.scaleBand()
                                .domain(d3.range(data.length))
                                .range([0 + margin.left, width - margin.right])
                                .round(true)
                                .paddingInner(0.1);

                const yScale = d3.scaleLinear()
                                .domain([0, d3.max(data, function(d) { return d.NetUnits; })])
                                .range([height - margin.bottom, 0]);

                // Y-AXIS
                d3.select(node).select('.yaxis')
                .attr('transform', 'translate(' + (xScale(0) - 4) + "," + (margin.top + 4) + ")") // - margin.left - 4
                .attr('class', 'yaxis')
                .call(d3.axisLeft(yScale)
                    .ticks(10, 's'));

                //X AXIS
                d3.select(node).select('.xaxis')
                .attr('transform', 'translate(' + (-2) +' , ' + (height - 4) + ")") //x: (-xScale.bandwidth()) y: - margin.bottom
                .attr('class', 'xaxis')
                .call(d3.axisBottom(xScale)
                    .ticks(data.length)
                    .tickFormat((d,i) => {return monthList[i];}))
                .selectAll("text")
                    .attr("y", 0)
                    .attr("x", 9)
                    .attr("dy", ".35em")
                    .attr("transform", "rotate(90)")
                    .style("text-anchor", "start");


                d3.select(node).selectAll('rect')
                    .data(data)
                    .exit()
                    .remove();

                //Update the graph
                const bars = d3.select(node).selectAll('rect')
                    .data(data);
                
                bars.enter()
                    .append('rect')
                    .attr('x', width)
                    .attr('y', function(d) { return yScale(d.NetUnits) + margin.bottom - 4; })
                    .attr("width", xScale.bandwidth())
                    .attr("height", (d) => {
                        return yScale(0) - yScale(d.NetUnits);
                    })
                    .attr('data-tip', (d) => {
                        return d.NetUnits + " Net Units";
                    })
                    .attr('data-for', 'svgTooltip')
                    .merge(bars)
                    .transition()
                    .duration(500)
                    .attr('x', function(d, i) { 
                        return xScale(i);
                    })
                    .attr('y', function(d) { return yScale(d.NetUnits) + margin.bottom - 4; })
                    .attr("width", xScale.bandwidth())
                    .attr("height", (d) => {
                        return yScale(0) - yScale(d.NetUnits);
                    })
                    .attr('data-tip', (d) => {
                        return d.NetUnits + " Net Units";
                    })
                    .attr('data-for', 'svgTooltip')
                    .style('fill', '#d62728');

            }
        }
    }

    render () {
        return (
            <div className="GraphBox">
                <svg className={this.props.graphID} ref={node => this.node = node}
                width={this.props.width} height={this.props.height}>
                </svg>
                <ReactTooltip id="svgTooltip" />
            </div>
        );
    }
}

// {/* <svg ref={node => this.node = node}
// width={this.props.width} height={this.props.height}>
// </svg> */}

export default BarGraph;